<template>
    <div>
        <div class="main-content__title"></div>
        <el-form :model="createFileForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm"
                 style="margin:20px;width:60%;min-width:600px;">
            <el-form-item label="" prop="name">
                <el-input v-model="createFileForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="description">
                <el-input type="textarea" v-model="createFileForm.description"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="back()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
  import ruleApi from '../../../api/rule'

  export default {
    name: 'app',
    data() {
      return {
        createFileForm: {
          name: '',
          checked: true,
          description: ''
        },
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 2, max: 50, message: ' 2  20 ', trigger: 'blur'}
          ],
          description: [
            {required: true, message: '', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            ruleApi.createFile(this.createFileForm.name, this.createFileForm.description).then((response) => {
              var serverData = response.data;
              if (serverData.code == 0) {
                // 
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.$router.push(`editrule?name=${this.createFileForm.name}`);
              } else {
                this.$message.error(`${serverData.msg}`);
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      back() {
        this.$router.push('rulefilelist');
      }
    }
  }

</script>



